import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const CreateTree = () => {
  const navigate = useNavigate();
  const [treeData, setTreeData] = useState({
    title: '',
    description: '',
    isPublic: true
  });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const response = await fetch('/api/trees', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`
        },
        body: JSON.stringify(treeData)
      });

      if (!response.ok) {
        throw new Error('트리 생성에 실패했습니다.');
      }

      const data = await response.json();
      navigate(`/trees/${data._id}/edit`);
    } catch (error) {
      console.error('트리 생성 오류:', error);
      alert('트리 생성에 실패했습니다. 다시 시도해주세요.');
    }
  };

  return (
    <div className="max-w-2xl mx-auto p-4">
      <div className="bg-white rounded-lg shadow-md p-6">
        <h1 className="text-2xl font-bold mb-6">새 트리 만들기</h1>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="title" className="block text-sm font-medium text-gray-700 mb-1">
              트리 이름
            </label>
            <input
              type="text"
              id="title"
              value={treeData.title}
              onChange={(e) => setTreeData({ ...treeData, title: e.target.value })}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="트리의 이름을 입력하세요"
              required
            />
          </div>

          <div>
            <label htmlFor="description" className="block text-sm font-medium text-gray-700 mb-1">
              트리 설명
            </label>
            <textarea
              id="description"
              value={treeData.description}
              onChange={(e) => setTreeData({ ...treeData, description: e.target.value })}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="트리에 대한 설명을 입력하세요"
              rows={4}
            />
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="isPublic"
              checked={treeData.isPublic}
              onChange={(e) => setTreeData({ ...treeData, isPublic: e.target.checked })}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label htmlFor="isPublic" className="ml-2 block text-sm text-gray-700">
              공개 트리로 만들기
            </label>
          </div>

          <div className="flex justify-end space-x-2">
            <button
              type="button"
              onClick={() => navigate(-1)}
              className="px-4 py-2 bg-gray-200 text-gray-800 rounded hover:bg-gray-300"
            >
              취소
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
            >
              트리 만들기
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default CreateTree; 