import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import App from './App';
import { store } from './store';
import './index.css'; // Tailwind styles
import '../styles/global.css'; // Global reset and base styles
import { initApiConfig } from './utils/apiConfig';

const queryClient = new QueryClient();

// API 설정을 초기화하는 래퍼 컴포넌트
const ApiConfigInitializer: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [isInitialized, setIsInitialized] = useState(false);

  useEffect(() => {
    const initialize = async () => {
      await initApiConfig();
      setIsInitialized(true);
    };

    initialize();
  }, []);

  // 초기화가 완료될 때까지 로딩 표시 (선택사항)
  if (!isInitialized) {
    return <div className="flex items-center justify-center h-screen">로딩 중...</div>;
  }

  return <>{children}</>;
};

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <Provider store={store}>
      <QueryClientProvider client={queryClient}>
        <BrowserRouter>
          <ApiConfigInitializer>
            <App />
          </ApiConfigInitializer>
        </BrowserRouter>
      </QueryClientProvider>
    </Provider>
  </React.StrictMode>
);
